.events {
  margin-bottom: 43px;

  @include on-tablet {
    margin-bottom: 4px;
  }

  @include on-desktop {
    margin-bottom: 98px;
  }

  &__title {
    font-size: 36px;
    font-weight: 700;
    line-height: 36px;
    text-align: center;
    margin-bottom: 40px;

    @include on-tablet {
      font-size: 48px;
      line-height: 48px;
      text-align: left;
      margin-bottom: 48px;
    }
  }
}
