.header {
  position: relative;

  &__bg {
    width: 100%;
    height: 320px;
    object-fit: cover;
    object-position: top;

    @include on-tablet {
      width: 374px;
      height: 384px;
    }

    @include on-desktop {
      width: 725px;
      height: 655px;
    }

    &-container {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: -1;
    }

    &-reference {
      position: relative;
      display: flex;
      justify-content: center;

      @include on-tablet {
        justify-content: flex-end;
      }
    }
  }

  &__logo {
    position: absolute;
    top: 8px;
    text-align: center;

    @include hover (transform, scale(1.1));

    @include on-tablet {
      right: 280px;
      font-size: 36px;
      line-height: 36px;
    }

    @include on-desktop {
      right: 600px;
    }
  }

  &__top {
    padding-top: 27px;

    @include on-tablet {
      padding-top: 39px;
    }
  }

  &__menu {
    background-image: url(../images/header/menu.svg);
  }

  &__title {
    font-size: 45px;
    font-weight: 700;
    line-height: 45px;
    text-align: center;
    color: #000;
    margin: 0;
    margin-top: 269px;

    @include on-tablet {
      font-size: 54px;
      line-height: 64.8px;
      text-align: left;
      margin-top: 64px;
    }

    @include on-desktop {
      font-size: 72px;
      line-height: 86.4px;
      margin-top: 143px;
    }
  }

  &__bottom {
    margin-top: 20px;
    display: flex;
    align-items: center;

    @include on-tablet {
      margin-top: 38px;
    }

    @include on-desktop {
      margin-top: 89px;
    }
  }

  &__section-title {
    display: none;

    @include on-desktop {
      display: flex;
      align-items: center;
      transform: rotate(270deg);
      transform-origin: bottom left;
      font-size: 14px;
      line-height: 14px;
    }

    &::before {
      content: '';
      display: inline-block;
      border-top: 1px solid #1a5a4c;
      width: 70px;
      height: 1px;
      margin-right: 20px;
    }
  }

  &__button {
    margin-bottom: 70px;

    @include hover (transform, scale(1.1));

    @include on-tablet {
      margin-bottom: 107px;
    }

    @include on-desktop {
      margin-bottom: 318px;
    }
  }
}
