.gallery {
  &__title {
    font-size: 36px;
    font-weight: 700;
    line-height: 36px;
    text-align: center;
    margin-bottom: 40px;

    @include on-tablet {
      font-size: 48px;
      line-height: 48px;
      text-align: left;
      margin-bottom: 48px
    }

    @include on-desktop {
      margin-bottom: 56px;
    }
  }

  &__content {
    @include page-grid;
    row-gap: 30px;
    margin-bottom: 20px;

    @include on-tablet {
      margin-bottom: 32px;
    }

    @include on-desktop {
      margin-bottom: 200px;
    }
  }

  &__img {
    height: 410px;
    width: 100%;
    object-fit: cover;
    object-position: center;

    @include hover (transform, scale(1.2));

    @include on-tablet {
      height: 543px;
    }

    &:nth-child(n + 2) {
      display: none;
    }

    &:nth-child(2) {
      @include on-tablet {
        display: inline-block;
      }
    }

    &:nth-child(n) {
      @include on-desktop {
        display: inline-block;
      }
    }

    &--1 {
      grid-column: 1 / -1;
      object-position: top;

      @include on-tablet {
        grid-column: 1 / 4;
      }

      @include on-desktop {
        height: 600px;
        grid-column: 1 / 5;
      }
    }

    &--2 {
      @include on-tablet {
        grid-column: 4 / -1;
      }

      @include on-desktop {
        height: 600px;
        grid-column: 5 / -1;
      }
    }

    &--3 {
      @include on-desktop {
        height: 360px;
        grid-column: 1 / 9;
      }
    }

    &--4 {
      @include on-desktop {
        height: 360px;
        grid-column: 9 / -1;
      }
    }
  }

  &__pagination {
    width: 102px;
    height: 9px;
    margin-bottom: 100px;

    @include on-tablet {
      margin-bottom: 92px;
    }

    @include on-desktop {
      display: none;
    }
  }
}
