.footer {
  &__content {
    @include page-grid;
    margin-top: 50px;
  }

  &__icons {
    grid-column: 1 / -1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;

    @include on-tablet {
      flex-direction: column;
      justify-content: initial;
      gap: 62px;
      grid-column: span 1;
      align-items: flex-start;
    }

    @include on-desktop {
      grid-column: span 2;
    }
  }

  &__social {
    display: flex;
    gap: 20px;

    @include on-tablet {
      gap: 32px;
    }

    @include on-desktop {
      gap: 22px;
    }

    &-img {
      height: 24px;
      width: 24px;

      @include hover (transform, scale(1.2));
    }
  }

  &__schedule {
    grid-column: 1 / 1;

    @include on-tablet {
      grid-column: 2 / 4;
    }

    @include on-desktop {
      grid-column: 3 / 5;
    }
  }

  &__contacts {
    grid-column: 2 / 2;
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include on-tablet {
      grid-column: 4 / 6;
    }

    @include on-desktop {
      grid-column: 5 / 7;
    }
  }

  &__up {
    display: inline-block;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    border: 1px solid $menu-bg-color;
    background-image: url(../images/footer/arrow-up.svg);
    background-position: center;
    background-repeat: no-repeat;

    @include hover (transform, scale(1.1));

    &-container {
      grid-column: 2 / 2;
      display: flex;
      justify-content: flex-end;

      @include on-tablet {
        display: none;
      }
    }
  }

  &__line {
    height: 1px;
    width: 100%;
    grid-column: 1 / -1;
    background-color: #1a5a4c50;
    margin-block: 20px 40px;

    @include on-tablet {
      grid-column: 2 / -2;
      margin-block: 64px;
    }

    @include on-desktop {
      width: 1px;
      height: 260px;
      grid-column: 7 / 7;
      margin: 0 auto;
    }
  }

  &__nav {
    width: 100%;

    &--1 {
      grid-column: 1 / 1;

      @include on-tablet {
        grid-column: 2 / 4;
      }

      @include on-desktop {
        grid-column: -6 / -4;
      }
    }

    &--2 {
      grid-column: 2 / 2;

      @include on-tablet {
        grid-column: 4 / 6;
      }

      @include on-desktop {
        grid-column: -4 / -2;
      }
    }
  }

  &__list {
    gap: 20px;
  }

  &__link {
    font-size: 14px;
    font-weight: 400;
    line-height: 14px;
    color: $main-color;
  }

  &__designed {
    margin-top: 80px;
    font-size: 12px;
    font-weight: 300;
    line-height: 16.88px;
    text-align: center;
    grid-column: 1 / -1;

    @include on-tablet {
      margin-top: 120px;
      grid-column: -3 / -1;
      text-align: right;
    }

    @include on-desktop {
      margin-top: 98px;
      text-align: left;
    }
  }

  &__copyright {
    font-size: 12px;
    font-weight: 300;
    line-height: 16.88px;
    text-align: center;
    grid-column: 1 / -1;
    margin-bottom: 128px;

    @include on-tablet {
      grid-column: 1 / 3;
      text-align: left;
      transform: translateY(-17px);
      margin-bottom: 18px;
    }

    @include on-desktop {
      grid-column: 1 / 4;
    }
  }
}
