.page {
  scroll-behavior: smooth;
  
  &__body {
    background-color: $main-bg-color;
    color: $main-color;
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateY(100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateY(0);
      pointer-events: all;
    }
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Montserrat, sans-serif;
  margin: 0;
}

p {
  font-family: 'IBM Plex Sans', sans-serif;
  margin: 0;
}
