.subscription {
  width: 100%;
  background-image: url(../images/subscription/background.png);
  background-size: cover;
  background-position: center;
  color: $main-bg-color;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 386px;

    @include on-tablet {
      height: 380px;
    }

    @include on-desktop {
      height: 550px;
    }
  }

  &__title {
    font-size: 36px;
    font-weight: 700;
    line-height: 36px;
    margin-bottom: 20px;

    @include on-tablet {
      font-size: 48px;
      line-height: 48px;
      margin-bottom: 16px;
    }
  }

  &__text {
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    text-align: center;
    margin-bottom: 50px;

    @include on-tablet {
      line-height: 18px;
      margin-bottom: 64px;
    }

    @include on-desktop {
      margin-bottom: 72px;
    }
  }

  &__form {
    display: flex;

    @include on-tablet {
      gap: 16px;
    }

    @include on-desktop {
      gap: 30px;
    }
  }

  &__input {
    box-sizing: border-box;
    height: 50px;
    width: 230px;
    font-family: Montserrat;
    font-size: 14px;
    line-height: 14px;
    padding-left: 24px;

    @include on-tablet {
      height: 70px;
      width: 374px;
    }

    @include on-desktop {
      width: 470px;
      padding-left: 32px;
    }
  }

  &__submit {
    cursor: pointer;
    height: 50px;
    width: 50px;
    border: none;
    background-color: #f14a27;
    background-image: url(../images/subscription/button.svg);
    background-repeat: no-repeat;
    background-position: center;

    @include hover (transform, scale(1.1));

    @include on-tablet {
      height: 70px;
      width: 70px;
    }
  }
}
