h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.page {
  scroll-behavior: smooth;
}

.page__body {
  color: #0f0e08;
  background-color: #fff;
  margin: 0;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: Montserrat, sans-serif;
}

p {
  margin: 0;
  font-family: IBM Plex Sans, sans-serif;
}

.container {
  margin: 0 auto;
  padding-inline: 20px;
}

@media (width >= 768px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .container {
    max-width: 1280px;
    margin-inline: auto;
    padding-inline: 56px;
  }
}

.top-bar {
  flex-direction: column;
  padding-top: 27px;
  display: flex;
}

@media (width >= 768px) {
  .top-bar {
    flex-direction: row;
    padding-top: 39px;
  }
}

.icon {
  width: 24px;
  height: 24px;
  display: block;
}

.button {
  color: #0f0e08;
  width: 100%;
  height: 50px;
  text-decoration: none;
  display: flex;
}

@media (width >= 768px) {
  .button {
    height: 70px;
  }
}

@media (width >= 1280px) {
  .button {
    margin-left: 55px;
  }
}

.button__text-block {
  box-sizing: border-box;
  border: 1px solid #1a5a4c;
  justify-content: center;
  align-items: center;
  width: calc(100% - 50px);
  height: 50px;
  display: flex;
}

@media (width >= 768px) {
  .button__text-block {
    width: 200px;
    height: 70px;
  }
}

.button__arrow {
  background-color: #1a5a4c;
  background-image: url("arrow.b5677e86.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
}

@media (width >= 768px) {
  .button__arrow {
    width: 70px;
    height: 70px;
  }
}

.logo {
  color: #0f0e08;
  font-family: Montserrat;
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;
  text-decoration: none;
}

.header {
  position: relative;
}

.header__bg {
  object-fit: cover;
  object-position: top;
  width: 100%;
  height: 320px;
}

@media (width >= 768px) {
  .header__bg {
    width: 374px;
    height: 384px;
  }
}

@media (width >= 1280px) {
  .header__bg {
    width: 725px;
    height: 655px;
  }
}

.header__bg-container {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header__bg-reference {
  justify-content: center;
  display: flex;
  position: relative;
}

@media (width >= 768px) {
  .header__bg-reference {
    justify-content: flex-end;
  }
}

.header__logo {
  text-align: center;
  transition: transform .3s;
  position: absolute;
  top: 8px;
}

.header__logo:hover {
  transform: scale(1.1);
}

@media (width >= 768px) {
  .header__logo {
    font-size: 36px;
    line-height: 36px;
    right: 280px;
  }
}

@media (width >= 1280px) {
  .header__logo {
    right: 600px;
  }
}

.header__top {
  padding-top: 27px;
}

@media (width >= 768px) {
  .header__top {
    padding-top: 39px;
  }
}

.header__menu {
  background-image: url("menu.3804cec3.svg");
}

.header__title {
  text-align: center;
  color: #000;
  margin: 269px 0 0;
  font-size: 45px;
  font-weight: 700;
  line-height: 45px;
}

@media (width >= 768px) {
  .header__title {
    text-align: left;
    margin-top: 64px;
    font-size: 54px;
    line-height: 64.8px;
  }
}

@media (width >= 1280px) {
  .header__title {
    margin-top: 143px;
    font-size: 72px;
    line-height: 86.4px;
  }
}

.header__bottom {
  align-items: center;
  margin-top: 20px;
  display: flex;
}

@media (width >= 768px) {
  .header__bottom {
    margin-top: 38px;
  }
}

@media (width >= 1280px) {
  .header__bottom {
    margin-top: 89px;
  }
}

.header__section-title {
  display: none;
}

@media (width >= 1280px) {
  .header__section-title {
    transform-origin: 0 100%;
    align-items: center;
    font-size: 14px;
    line-height: 14px;
    display: flex;
    transform: rotate(270deg);
  }
}

.header__section-title:before {
  content: "";
  border-top: 1px solid #1a5a4c;
  width: 70px;
  height: 1px;
  margin-right: 20px;
  display: inline-block;
}

.header__button {
  margin-bottom: 70px;
  transition: transform .3s;
}

.header__button:hover {
  transform: scale(1.1);
}

@media (width >= 768px) {
  .header__button {
    margin-bottom: 107px;
  }
}

@media (width >= 1280px) {
  .header__button {
    margin-bottom: 318px;
  }
}

.nav {
  width: 100px;
}

@media (width >= 1280px) {
  .nav {
    width: 168px;
  }
}

.nav__list {
  flex-direction: column;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__link {
  font-family: IBM Plex Sans;
  text-decoration: none;
}

.schedule {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.schedule__workdays {
  font-size: 14px;
  font-weight: 600;
  line-height: 21px;
}

.schedule__title {
  line-height: 19.69px;
}

.schedule__span {
  padding: 0;
  font-weight: 400;
}

.menu {
  box-sizing: border-box;
  --content-height: 399px;
  z-index: 2;
  overflow: auto;
}

@media (width >= 768px) {
  .menu {
