  }
}

@media (width >= 1280px) {
  .menu {
    --content-height: 233px;
  }
}

.menu__content {
  height: var(--content-height);
  background-color: #1a5a4c;
}

.menu__bg {
  height: calc(100vh - var(--content-height));
  opacity: .5;
  background-color: #18181a;
}

.menu__close {
  background-image: url("close-cross.ee68a0c1.svg");
}

.menu__page-links {
  display: flex;
}

.menu__nav {
  gap: 28px;
  margin-left: 28px;
  display: flex;
}

@media (width >= 768px) {
  .menu__nav {
    gap: 20px;
    margin-inline: 38px;
  }
}

@media (width >= 1280px) {
  .menu__nav {
    gap: 31px;
    margin-inline: 102px;
  }
}

.menu__list {
  gap: 24px;
}

@media (width >= 1280px) {
  .menu__list {
    gap: 30px;
  }
}

.menu__link {
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
}

@media (width >= 1280px) {
  .menu__link {
    font-size: 22px;
    line-height: 22px;
  }
}

.menu__schedule {
  color: #fff;
  border-top: 1px solid #fff;
  margin-top: 24px;
  padding-top: 24px;
}

@media (width >= 768px) {
  .menu__schedule {
    border: none;
    border-left: 1px solid #fff;
    margin-top: 0;
    padding-top: 0;
    padding-left: 38px;
  }
}

@media (width >= 1280px) {
  .menu__schedule {
    padding-left: 102px;
  }
}

.menu__schedule-content {
  flex-direction: column;
  margin-top: 20px;
  display: flex;
}

@media (width >= 768px) {
  .menu__schedule-content {
    flex-direction: row;
    gap: 26px;
  }
}

@media (width >= 1280px) {
  .menu__schedule-content {
    gap: 30px;
  }
}

@media (width >= 768px) {
  .menu__workdays {
    width: 170px;
  }

  .menu__offdays {
    width: 144px;
  }
}

@media (width >= 1280px) {
  .menu__offdays {
    width: 170px;
  }
}

.events {
  margin-bottom: 43px;
}

@media (width >= 768px) {
  .events {
    margin-bottom: 4px;
  }
}

@media (width >= 1280px) {
  .events {
    margin-bottom: 98px;
  }
}

.events__title {
  text-align: center;
  margin-bottom: 40px;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
}

@media (width >= 768px) {
  .events__title {
    text-align: left;
    margin-bottom: 48px;
    font-size: 48px;
    line-height: 48px;
  }
}

.performance {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  margin-bottom: 60px;
  transition: transform .3s;
  display: grid;
}

@media (width >= 768px) {
  .performance {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .performance {
    --columns: 12;
  }
}

.performance:hover {
  transform: scale(1.1);
}

@media (width >= 1280px) {
  .performance {
    margin-bottom: 104px;
  }
}

@media (width >= 768px) {
  .performance:first-of-type {
    margin-bottom: 64px;
  }

  .performance:last-of-type {
    margin-bottom: 112px;
  }
}

.performance__img {
  object-fit: cover;
  object-position: center;
  grid-column: 1 / -1;
  width: 100%;
  height: 210px;
  margin-bottom: 16px;
}

@media (width >= 768px) {
  .performance__img {
    height: 520px;
    margin-bottom: 40px;
  }
}

@media (width >= 1280px) {
  .performance__img {
    grid-column: 1 / 8;
    margin-bottom: 0;
  }
}

.performance__text {
  grid-column: 1 / -1;
}

@media (width >= 768px) {
  .performance__text {
    grid-column: 1 / 5;
  }
}

@media (width >= 1280px) {
  .performance__text {
    grid-column: -5 / -1;
  }
}

.performance__date-and-type {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 10px;
  display: flex;
}

@media (width >= 768px) {
  .performance__date-and-type {
    width: 220px;
    margin-bottom: 30px;
  }
}

.performance__type {
  color: #687480;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
}

@media (width >= 768px) {
  .performance__type {
    font-size: 14px;
    line-height: 14px;
  }
}

.performance__date {
  color: #1a5a4c;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}

@media (width >= 768px) {
  .performance__date {
    font-size: 14px;
    line-height: 21px;
  }
}

.performance__title {
  align-items: center;
  font-size: 28px;
  font-weight: 400;
  line-height: 28px;
  display: flex;
}

@media (width >= 768px) {
  .performance__title {
    font-size: 36px;
    line-height: 36px;
  }
}

.performance__title:after {
  content: "";
  background-color: #f14a27;
  width: 15px;
  height: 15px;
  margin-left: 10px;
  display: inline-block;
}

.performance__description {
  color: #4e4e4e;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
}

.lecture {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.lecture__img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 210px;
}

@media (width >= 768px) {
  .lecture__img {
    width: calc(100% - 68px);
    height: 390px;
    margin-inline: auto;
  }
}

@media (width >= 1280px) {
  .lecture__img {
    width: 100%;
    height: 550px;
  }
}

.lecture__text {
  background-color: #fff;
  width: 280px;
  height: 140px;
  transform: translateY(-57px);
