    transform: translateY(-102px);
  }
}

.lecture__date-and-type {
  justify-content: space-between;
  align-items: center;
  margin-block: 20px;
  margin-inline: 21px;
  display: flex;
}

@media (width >= 768px) {
  .lecture__date-and-type {
    margin-block: 45px 16px;
    margin-inline: 100px;
  }
}

.lecture__title {
  text-align: center;
  font-size: 28px;
  font-weight: 400;
