    font-size: 48px;
    line-height: 48px;
  }
}

@media (width >= 1280px) {
  .gallery__title {
    margin-bottom: 56px;
  }
}

.gallery__content {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  gap: 30px 20px;
  margin-bottom: 20px;
  display: grid;
}

@media (width >= 768px) {
  .gallery__content {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .gallery__content {
    --columns: 12;
  }
}

@media (width >= 768px) {
  .gallery__content {
    margin-bottom: 32px;
  }
}

@media (width >= 1280px) {
  .gallery__content {
    margin-bottom: 200px;
  }
}

.gallery__img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 410px;
  transition: transform .3s;
}

.gallery__img:hover {
  transform: scale(1.2);
}

@media (width >= 768px) {
  .gallery__img {
    height: 543px;
  }
}

.gallery__img:nth-child(n+2) {
  display: none;
}

@media (width >= 768px) {
  .gallery__img:nth-child(2) {
    display: inline-block;
  }
}

@media (width >= 1280px) {
  .gallery__img:nth-child(n) {
    display: inline-block;
  }
}

.gallery__img--1 {
  object-position: top;
  grid-column: 1 / -1;
}

@media (width >= 768px) {
  .gallery__img--1 {
    grid-column: 1 / 4;
  }
}

@media (width >= 1280px) {
  .gallery__img--1 {
    grid-column: 1 / 5;
    height: 600px;
  }
}

@media (width >= 768px) {
  .gallery__img--2 {
    grid-column: 4 / -1;
  }
}

@media (width >= 1280px) {
  .gallery__img--2 {
    grid-column: 5 / -1;
    height: 600px;
  }

  .gallery__img--3 {
